.comm-container {
    height: calc(100vh - 120px);
    margin-top: 75px;
}

.comm-part {
    height: calc(100vh - 240px);
    overflow: auto;
}

.comtable {
    background: #ffffff;
    padding: 20px;
    border-radius: 8px;
}

.p-btn {
    font-family: gothambook, sans-serif;
    background: #fff;
    padding: 14px 65px;
    border-radius: 25px;
    font-size: 20px;
    font-weight: 700;
}

.center-head {
    position: relative;
}

.backBtn {
    color: #007bff;
    text-decoration: underline;
    font-weight: bold;
}

.iconquestion {
    position: absolute;
    right: 0;
    top: 40%;
    cursor: pointer;
    transform: translate(0, -40%);
}

@media screen and (max-width: 767px) {
    .p-btn {
        font-family: gothambook, sans-serif;
        background: #fff;
        padding: 8px 20px;
        border-radius: 25px;
        font-size: 15px;
        font-weight: 700;
    }

    .comm-container {
        margin-top: 20px;
        padding: 10px;
    }
}

.sendBtn {
    background: #ff6400;
    color: #ffffff;
    border: 1px solid #ff6400;
    padding: 0 25px;
    margin-top: 15px;
}

#outerContainer {
    position: relative;
    display: inline-block;
}

.pdf-image {
    width: 200px;
    height: auto;
    overflow: hidden;
    border: 1px solid #80808024;
    margin-top: 20px;
}

.pdf-image img {
    width: 100%;
}

.pdf-spinner {
    position: absolute;
    top: 19%;
    left: 20%;
    z-index: 9;
    text-align: center;
}

.comtable ::ng-deep #clearFormatting- {
    background-image: url("../../../../assets/img/text.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 17px 17px;
}

.comtable ::ng-deep #clearFormatting- .fa-remove {
    display: none;
}

.comtable ::ng-deep .angular-editor {
    position: relative;
}

.comtable ::ng-deep angular-editor-toolbar {
    position: absolute;
    bottom: 10px;
    left: 10px;
}

.comtable ::ng-deep .angular-editor-textarea {
    padding-bottom: 5rem !important;
}

.comtable ::ng-deep .email-content {
    max-height: 300px !important;
    overflow-y: auto;
}

.con-head {
    font-size: 16px;
    font-weight: bold;
    color: #aba3a3;
}

.editor-wrap>p {
    font-weight: bold;
    width: 13%;
    font-size: 15px;
}

.comtable ::ng-deep .mat-form-field-appearance-outline .mat-form-field-infix {
    padding: 0px 0px 1px 0px !important;
}

.comtable ::ng-deep .mat-form-field-appearance-outline .mat-form-field-flex {
    height: 48px;
}

.comtable ::ng-deep input.mat-input-element {
    margin-left: 0 !important;
}

.redBorder {
    border: 1px solid red !important;
}
.errdiv p:first-child {
    width: 15%;
}
.validation-error {
    color: red;
    width: 84%;
}

.comtable ::ng-deep .redBorder .angular-editor .angular-editor-textarea {
    border: 2px solid red !important;
}

/* ::ng-deep .redBorder .angular-editor .angular-editor-textarea:focus-visible {
    border: 2px solid red !important;
} */
.comtable ::ng-deep .angular-editor-textarea:focus-within { outline: none !important; }
.comtable ::ng-deep .angular-editor-textarea:focus-visible { outline: none !important; }

.pdfbottom {
    position: relative;
    width: 216px;
}
.pdfbottom img {
    width: 100%;
    cursor: pointer;
}
.pdfImg {
    border: 1px solid #eee;
    cursor: pointer;
}
.pdfbottom p {
    position: absolute;
    top: 10px;
    left: 42px;
    font-size: 16px;
    z-index: 9999;
    white-space: nowrap;
    width: 120px;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: auto;
}
::ng-deep .mat-tooltip.below {
    width: auto;
}
.viewemail .editor-wrap:nth-child(3) p,
 .viewemail .editor-wrap:nth-child(3) span {
    color: 
    #3E3E3E;
    font-weight: bold;
    font-size: 18px;
}
.viewemail .editor-wrap:nth-child(1) p,
 .viewemail .editor-wrap:nth-child(2) p,
  .viewemail .editor-wrap:nth-child(1) span,
  .viewemail .editor-wrap:nth-child(2) span {
    color: #3E3E3E;
    font-weight: bold;
    font-size: 14px;
}
.viewemail .editor-wrap > p {
    width: initial;
    margin-right: 8px;
}
.viewemail .editor-wrap:nth-child(3) + div {
    background: #f7f7f7;
    border-radius: 3px 3px 0 0;
    padding: 8px 15px;
}
article.viewemail + form {
    background: #f7f7f7;
    padding: 10px 15px;
    border-radius: 0 0 3px 3px;
}